import { ElMessage } from 'element-plus'

export const socketListeners = (events, store) => {
  const unsubscribers = []

  const onDeviceDiagnostic = (data) => {
    const { commit } = store

    commit('user/SET_BBB_SCAN', data, { root: true })
  }
  const onShippingLabel = (data) => {
    const { commit } = store

    commit('buyback/online/SET_SHOW_SHIPPING_LABEL_BY_EVENT', data, {
      root: true
    })
  }
  const onDataFeedProcess = (data) => {
    const { dispatch, commit } = store

    if (!data?.data) return
    if (data.data.progress === 100) setTimeout(async () => {
      await dispatch('user/me', null, {
        root: true
      })
      ElMessage({
        type: 'success',
        message: 'Upload Successful!'
      })
      dispatch('notification/closeNotification', 'NotificationDatafeed', { root: true })
    }, 1000)
    commit('notification/OPEN_NOTIFICATION', 'NotificationDatafeed', {
      root: true
    }),
    commit('integration/SET_DATA_FEED_PROGRESS', data.data, {
      root: true
    })
  }
  const onBookPriceProcess = (data) => {
    const { commit, dispatch } = store
    if (!data?.data) return
    if (data.data.progress === 100)
      setTimeout(async () => {
        ElMessage({
          type: 'success',
          message: 'Upload Successful!'
        })
        dispatch('notification/closeNotification', 'NotificationPricingBooks', { root: true })
        dispatch('notification/closeNotification', 'NotificationPricingBooksInvalid', { root: true })
        commit('product/book/SET_PROCESSING_PROGRESS', {
          progress: 0
        }, { root: true })
        await Promise.all([
          dispatch('user/me', null, {
            root: true
          }),
          dispatch('setting/buybackDevice/categories', null, { root: true })
        ])
        commit('notification/SET_NOTIFICATION_CLOSABLE', {
          name: 'NotificationPricingBooks',
          closable: true
        }, { root: true })
        const hasInvalidData = data.data.is_invalid_data == 1
        if (hasInvalidData)
          commit(
            'notification/OPEN_NOTIFICATION',
            'NotificationPricingBooksInvalid',
            { root: true }
          )
      }, 1500)
    commit('product/book/SET_PROCESSING_PROGRESS', data.data, { root: true })
  }
  const onGhlUnInstall = (data) => {
    const { commit } = store
    if (!data || !data.data) {
      commit('integration/ghl/SET_UNINSTALL', false, { root: true })
    } else commit('integration/ghl/SET_UNINSTALL', data, { root: true })
  }
  const onOfferHasChanges = (data) => {
    const { commit } = store
    if (!data?.data) return
    ElMessage({
      message: data.data.offer_changes_notes
    })
    commit('buyback/SET_SHOW_ORDER_TO_RELOADS', data.data, { root: true })
  }
  const onShippingKitBatch = (data) => {
    const { dispatch, commit } = store
    if (!data?.data) return
    if (data.data.progress > 0 && data.data.progress < 100) {
      dispatch('notification/closeNotification', 'NotificationShippingKitDownload', { root: true })
      commit('shipping/SET_PROCESSING_DOWNLOAD_SHIPPING_KIT_PROGRESS', {
        progress: data.data?.progress || 0,
        path: data.data?.path || null,
        type: data.data?.file || 'zip',
        loading: true
      }, { root: true })
      commit(
        'notification/OPEN_NOTIFICATION',
        'NotificationShippingKitDownload',
        { root: true }
      )
      commit('notification/SET_NOTIFICATION_CLOSABLE', {
        name: 'NotificationShippingKitDownload',
        closable: false
      }, { root: true })
    } else if (data.data.progress === 100) {
      commit('shipping/SET_PROCESSING_DOWNLOAD_SHIPPING_KIT_PROGRESS', {
        progress: data.data?.progress,
        path: data.data?.path,
        type: data.data?.file,
        loading: true
      }, { root: true })
      commit('notification/SET_NOTIFICATION_CLOSABLE', {
        name: 'NotificationShippingKitDownload',
        closable: true
      }, { root: true })
    }
  }
  const onAlertMessage = (data) => {
    if (!data?.data) return

    ElMessage({
      message: data.data.message
    })
  }
  const onImportPricingProgres = (data) => {
    const { dispatch, commit } = store
    if (!data?.data) return
    if (data.data.progress === 100) {
      setTimeout(async () => {
        ElMessage({
          type: 'success',
          message: 'Import Successful!'
        })
        dispatch('notification/closeNotification', 'NotificationPriceUpload', { root: true })
        dispatch('notification/closeNotification', 'NotificationPriceUploadInvalid', { root: true })
        commit('pricing/SET_PROCESSING_UPLOAD_PROGRESS', {
          progress: 0
        }, { root: true })
        const res = await Promise.all([
          dispatch('pricing/getPriceUploadInvalidCheck', null, { root: true }),
          dispatch('user/me', null, {
            root: true
          })
        ])
        commit('notification/SET_NOTIFICATION_CLOSABLE', {
          name: 'NotificationPriceUpload',
          closeable: true
        }, { root: true })
        const hasInvalidData = (res[0]?.data?.is_failed || 0) == 1
        commit('pricing/SET_PROCESSING_UPLOAD_INVALID', {
          invalid: hasInvalidData,
          total: res[0]?.data?.total_rows_invalid || 0,
          date: res[0]?.data?.last_update || null,
          type: res[0]?.data.type || 'CSV'
        }, { root: true })
        if (hasInvalidData) {
          commit(
            'notification/OPEN_NOTIFICATION',
            'NotificationPriceUploadInvalid',
            { root: true }
          )
        }
      }, 1500)
    }
    commit('pricing/SET_PROCESSING_UPLOAD_PROGRESS', data.data, { root: true })
  }
  const onExportPricingProgress = (data) => {
    const { dispatch, commit } = store
    if (!data?.data) return
    if (data.data.progress > 0 && data.data.progress < 100) {
      dispatch('notification/closeNotification', 'NotificationPriceUploadDownload', { root: true })
      commit('pricing/SET_PROCESSING_DOWNLOAD_PROGRESS', {
        progress: data.data?.progress || 0,
        path: data.data?.path || null,
        type: data.data?.file || 'csv',
        loading: true
      }, { root: true })
      commit(
        'notification/OPEN_NOTIFICATION',
        'NotificationPriceUploadDownload',
        { root: true }
      )
      commit('notification/SET_NOTIFICATION_CLOSABLE', {
        name: 'NotificationPriceUploadDownload',
        closable: false
      }, { root: true })
    } else if (data.data.progress === 100) {
      commit('pricing/SET_PROCESSING_DOWNLOAD_PROGRESS', {
        progress: data.data?.progress,
        path: data.data?.path,
        type: data.data?.file,
        loading: true
      }, { root: true })
      commit('notification/SET_NOTIFICATION_CLOSABLE', {
        name: 'NotificationPriceUploadDownload',
        closable: true
      }, { root: true })
    }
  }
  const onExportPricingFailedProgress = (data) => {
    const { dispatch, commit } = store
    if (!data?.data) return
    if (data.data.progress > 0 && data.data.progress < 100) {
      dispatch('notification/closeNotification', 'NotificationPriceUploadDownloadInvalid', { root: true })
      commit('pricing/SET_PROCESSING_DOWNLOAD_PROGRESS_INVALID', {
        progress: data.data?.progress || 0,
        path: data.data?.path || null,
        type: data.data?.file || 'csv',
        loading: true
      }, { root: true })
      commit(
        'notification/OPEN_NOTIFICATION',
        'NotificationPriceUploadDownloadInvalid',
        { root: true }
      )
      commit('notification/SET_NOTIFICATION_CLOSABLE', {
        name: 'NotificationPriceUploadDownloadInvalid',
        closable: false
      }, { root: true })
    } else if (data.data.progress === 100) {
      commit('pricing/SET_PROCESSING_DOWNLOAD_PROGRESS_INVALID', {
        progress: data.data?.progress,
        path: data.data?.path,
        type: data.data?.file,
        loading: true
      }, { root: true })
      commit('notification/SET_NOTIFICATION_CLOSABLE', {
        name: 'NotificationPriceUploadDownloadInvalid',
        closable: true
      }, { root: true })
    }
  }
  const onNotification = (data) => {
    const { commit } = store
    if (!data?.data) return
      
    commit('notification/SET_HAS_UNREAD_NOTIFICATION', data.data, { root: true })
  }

  events.subscribe('notification', onNotification)
  unsubscribers.push(() => events.unsubscribe('notification', onNotification))

  events.subscribe('import-pricing-progress', onImportPricingProgres)
  unsubscribers.push(() => events.unsubscribe('import-pricing-progress', onImportPricingProgres))

  events.subscribe('export-pricing-progress', onExportPricingProgress)
  unsubscribers.push(() => events.unsubscribe('export-pricing-progress', onImportPricingProgres))

  events.subscribe('export-pricing-failed-progress', onExportPricingFailedProgress)
  unsubscribers.push(() => events.unsubscribe('export-pricing-failed-progress', onExportPricingFailedProgress))
  
  events.subscribe('alert-message', onAlertMessage)
  unsubscribers.push(() => events.unsubscribe('alert-message', onAlertMessage))
  
  events.subscribe('shipping-kit-batch', onShippingKitBatch)
  unsubscribers.push(() => events.unsubscribe('shipping-kit-batch', onShippingKitBatch))
  
  events.subscribe('offer-has-changes', onOfferHasChanges)
  unsubscribers.push(() => events.unsubscribe('offer-has-changes', onOfferHasChanges))
  
  events.subscribe('ghl-uninstall', onGhlUnInstall)
  unsubscribers.push(() => events.unsubscribe('ghl-uninstall', onGhlUnInstall))

  events.subscribe('device-diagnostic', onDeviceDiagnostic)
  unsubscribers.push(() => events.unsubscribe('device-diagnostic', onDeviceDiagnostic))

  events.subscribe('shipping-label', onShippingLabel)
  unsubscribers.push(() => events.unsubscribe('shipping-label', onShippingLabel))

  events.subscribe('datafeed-process', onDataFeedProcess)
  unsubscribers.push(() => events.unsubscribe('datafeed-process', onDataFeedProcess))

  events.subscribe('books-pricing-process', onBookPriceProcess)
  unsubscribers.push(() => events.unsubscribe('books-pricing-process'), onBookPriceProcess)

  return () => {
    unsubscribers.forEach(unsub => unsub())
    unsubscribers.length = 0
  }
}
