import { socket, events } from 'constants/socket'
import { socketListeners } from 'utils/socket-listeners'

const state = () => ({
  token: undefined,
  company_token: undefined,
  channel: undefined,
  tenant_id: undefined
})

const getters = {}

const mutations = {
  SET_TOKEN(state, data) {
    state.token = data
  },
  SET_COMPANY_TOKEN(state, data) {
    state.company_token = data
  },
  SET_SOCKET_CHANNEL(state, data) {
    state.channel = data
  },
  SET_TENANT_ID(state, data) {
    state.tenant_id
  }
}

let cleanupListeners = null
let isConnected = false

const actions = {
  init({ state, rootState }, payload) {
    socket.init({
      url: payload?.url ?? window.localStorage.getItem('region_socket'),
      token: state.token,
      buyback_company_token: state.company_token
    })
  },
  connect({ state, commit, dispatch }) {
    if (isConnected) return
    isConnected = true

    if (cleanupListeners) cleanupListeners()

    cleanupListeners = socketListeners(events, { commit, dispatch })

    socket.connecting(state.channel, state.tenant_id)
  },
  disconnect() {
    if (cleanupListeners) {
      cleanupListeners()
      cleanupListeners = null
    }
    isConnected = false
  }
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}