<template>
  <SplashLoading />
  <NotificationPopup />
  <Paywall
    v-model:show="statePaywall.show"
    :title="statePaywall.title"
    :description="statePaywall.description"
    :image-url="statePaywall.image_url"
    :package-name="statePaywall.package_name"
  />
  <router-view />
</template>
<script>
import { defineComponent, onMounted, onUnmounted, ref } from 'vue'

import { usePaywall } from 'composable/paywall'

import SplashLoading from 'components/SplashLoading.vue'
import Paywall from 'components/Paywall.vue'
import NotificationPopup from 'components/Notification/NotificationPopup.vue'

import { initSessionKey, sessionListener } from 'utils/session'
import { ElMessage } from 'element-plus'

export default defineComponent({
  components: {
    SplashLoading,
    Paywall,
    NotificationPopup
  },
  setup() {
    const { state: statePaywall } = usePaywall()
    const messageShownOnline = ref(false)
    const messageShownOffline = ref(false)
    const onOffline = () => {
      if (messageShownOffline.value) return
      ElMessage({
        message: 'You are currently offline. Please check your internet connection',
        type: 'warning'
      })
      messageShownOffline.value = true
      messageShownOnline.value = false // reset
    }

    const onOnline = () => {
      if (messageShownOnline.value) return
      ElMessage({
        message: 'You are back online',
        type: 'success'
      })
      messageShownOnline.value = true
      messageShownOffline.value = false // reset
    }

    onMounted(async () => {
      initSessionKey()
      window.addEventListener('storage', sessionListener)
      window.addEventListener('online', onOnline)
      window.addEventListener('offline', onOffline)
    })

    onUnmounted(() => {
      window.removeEventListener('storage', sessionListener)
      window.removeEventListener('online', onOnline)
      window.removeEventListener('offline', onOffline)
    })

    return {
      statePaywall
    }
  }
})
</script>
