import axios from 'axios'
import { isArrayBuffer, isArrayBufferView } from 'axios/lib/utils'
import store from 'store'
import router from 'router'
import { apiMarketplace } from 'constants/setting'
import { ElMessage } from 'element-plus'
import { upperCaseFirstLetter } from 'utils/string'

const requestQueue = new Map()
const instance = axios.create({
	baseURL: import.meta.env.VITE_BASE_URL_API || 'localhost:8000',
	// timeout: 10000,
	withCredentials: true
})

instance.interceptors.request.use(config => {
	const isRegionDomainExist = !!window.localStorage.getItem('region_domain')

	if (JSON.parse(window.localStorage.getItem('white-label')) && !isRegionDomainExist) {
		config.baseURL = window.API_WHITELABEL_BASEURL
	}
	else if (isRegionDomainExist) {
		config.baseURL = `${window.localStorage.getItem('region_domain')}/api`
	}
	else config.baseURL = import.meta.env.VITE_BASE_URL_API

	const requestUniqueId = Math.floor(Math.random() * 1000000)
	config.id = requestUniqueId
	requestQueue.set(requestUniqueId, config)

	if (config.method === 'get' && store.state.setting.marketplace.marketplace_filter) {
		const splitURL = config.url.split('/').filter((_, key, self) => key !== self.length - 1)
		if (apiMarketplace.includes(splitURL.join('/'))) {
			const { currentRoute } = router
			const marketerId =
				currentRoute.value.params.marketerId ||
				store.state.setting.marketplace.marketplace_filter.id
			config.params = {
				...config.params,
				...(marketerId && { marketer_id: marketerId })
			}
		}
	}
	// if(store.state.loggedIn) config.headers['x-request-token'] = store.state.companyToken
	return config
})

instance.interceptors.response.use(
	async response => {
		// ADD: Retry once if re_post === true and hasn't retried yet
		if (
      (response?.data?.re_login === true || response?.data?.re_post === true) &&
      response?.data?.domain &&
      !response.config._retry
    ) {
			window.localStorage.setItem('region_domain', response.data.domain)
			window.localStorage.setItem('region_socket', response.data.socket)

			const originalRequest = { ...response.config }

			originalRequest._retry = true

      return await instance(originalRequest)
    }
    // END ADD Retry

		requestQueue.delete(response.config.id)
		if (response.status >= 500) {
			// TODO: Show message box error
			// TODO: Reset token
			location.reload()
			return Promise.reject(new Error(response.statusText || 'Error'))
		} else return response.data
	},
	error => {
		if (!navigator.onLine) return Promise.reject(error)
		if (error.response) requestQueue.delete(error.response.config.id)
		if (error.name === 'AbortError' || error.code === 'ERR_CANCELED') {
      return new Promise(() => {})
    }
		if (axios.isCancel(error)) return new Promise(() => {})
		if (error?.response?.config?.url.includes('login-status')) {
			if (router.currentRoute.value.name !== 'error' && error.response.status == 503) {
				router.push({
					name: 'error',
					params: { code: error.response.status },
					query: { message: error.response.data.message || 'Unknown error occurred' }
				})
				return Promise.reject(error)
			}
		}

		// Convert response to JSON if error response is typeof arraybuffer
		if (error.response?.data) {
			if (isArrayBuffer(error.response.data) || isArrayBufferView(error.response.data)) {
				if (error.response) {
					const decodedString = new TextDecoder('utf-8').decode(error.response.data)
					try {
						error.response.data = JSON.parse(decodedString)
					} finally {
					}
				}
			}
		}

		const { message: errorMessage, type: errorType } = extractErrorMessage(error)
		ElMessage({
			message: errorMessage,
			type: errorType,
			dangerouslyUseHTMLString: true
		})

		if (error.response?.status === 401 || error.response?.status_code === 401) {
			store.dispatch('user/logout')
			router.push({
				name: 'login',
				replace: true
			})
		}
		return Promise.reject(error)
	}
)

export const CancelToken = axios.CancelToken
export const queue = requestQueue
export default instance
export const tryCatchRequest = async (state, callback, options) => {
	let response = undefined

	try {
		if (!state.isLoading) state.isLoading = true

		if (state.error) state.error = undefined

		if (state.controller) {
			state.controller.abort()
		}

		state.controller = new AbortController()

		response = await callback()

		state.isLoading = false

		return response
	} catch (e) {
		if (state.error) state.error = e.message || options.errorDefaultMessage
		state.isLoading = false

		throw e
	} finally {
		// if (typeof state.isLoading === 'boolean') state.isLoading = false
		state.isLoading = false
	}
}
export const extractErrorMessage = (error) => {
  const data = error?.response?.data
  const statusCode = error?.response?.status

  const raw =
    data?.errors?.mapping ??
    data?.errors?.message ??
    data?.message ??
    data?.error?.message

  let message = null
  let errorType = 'error'

  if (Array.isArray(raw)) {
    message = raw.join('\n')
  } else if (typeof raw === 'string') {
    message = upperCaseFirstLetter(raw)
  }

  // Fallback
  if (!message) {
    if ([400, 409, 422].includes(statusCode)) {
      message = 'We couldnt process your request. Please try again.'
      errorType = 'warning'
    } else {
      message = 'Something wrong, please try again!'
    }
  }

  return {
    message,
    type: errorType
  }
}
export const cleanupCacheByPrefix = (cache, prefix) => {
	for (const [key] of cache.entries()) {
    if (key.startsWith(`${prefix}/`)) {
      cache.delete(key)
    }
  }
}
export const cleanupCacheByUrl = (cache, url, newParams) => {
  const newParamString = new URLSearchParams(newParams).toString()

  for (const [key] of cache.entries()) {
    const [urlPart, paramString = ''] = key.split('?')

    if (urlPart === url && paramString !== newParamString) {
      cache.delete(key)
    }
  }
}
export const generateTrackingKey = (url, params) => {
  const query = new URLSearchParams(params).toString();
  return query ? `${url}?${query}` : url;
}
export const shouldFetch = (cache, key, ttl = 15 * 60 * 1000 /* 15 minute */) => {
  const now = Date.now()
  const last = cache.get(key)
  const should = !last || now - last > ttl

  return { should, now }
}
